import DdzBaseView from "./DdzBaseView";
import { DdzAudioConst } from "../data/DdzPathHelper";
import DdzDriver from "../DdzDriver";
import DdzGameConst from "../data/DdzGameConst";

/**
 * 左侧方设置，规则，退出等功能按钮
 */
export default class DdzSettleView extends DdzBaseView {
    private winView: DdzSettleContent;
    private loseView: DdzSettleContent;
    private closeBtn: cc.Node;
    private exitBtn: cc.Node;
    private gameBtn: cc.Node;
    private clickLimit = false;
    private timer: NodeJS.Timeout;

    constructor(node) {
        super();
        this.setNode(node);
    }

    protected initView() {
        this.winView = new DdzSettleContent(this.getChild('contentNode/winNode'));
        this.winView.active = false;
        this.loseView = new DdzSettleContent(this.getChild('contentNode/loseNode'));
        this.loseView.active = false;
        this.closeBtn = this.getChild('contentNode/closeBtn');
        this.closeBtn.active = false;
        DdzGameConst.addCommonClick(this.closeBtn, "", this.closeView, this);
        this.exitBtn = this.getChild('contentNode/exitBtn');
        this.exitBtn.active = false;
        DdzGameConst.addCommonClick(this.exitBtn, "", this.onExitClick, this);
        this.gameBtn = this.getChild('contentNode/gameBtn');
        this.gameBtn.active = false;
        DdzGameConst.addCommonClick(this.gameBtn, "", this.onGameClick, this);
    }
    
    protected onOpen(){
        this.delayShowActionBtn(5000);         // 以防没收到服务器协议情况
    }

    public delayShowActionBtn(delay: number){
        this.timer = setTimeout(() => {
            this.showActionBtn(true);
         }, delay);
    }


    public playSettleAnim(isWin: boolean, detail: any = {}) {
        let arr = detail.user_calcs || [];
        if (isWin) {
            this.winView.active = true;
            this.winView.showData(arr, isWin, detail.land_chair);
        }
        else {
            this.loseView.active = true;
            this.loseView.showData(arr, isWin, detail.land_chair);
        }
    }

    public showActionBtn(flag: boolean){
        this.closeBtn.active = flag;
        this.exitBtn.active = flag;
        this.gameBtn.active = flag;
		if(flag)
        	clearTimeout(this.timer);
    }

    // public showSettleAction(timeout: number = 2) {
    //     Game.Component.scheduleOnce(() => {
    //         this.closeBtn.active = true;
    //         this.exitBtn.active = true;
    //         this.gameBtn.active = true;
    //     }, timeout);
    // }

    protected onClose() {
        this.winView.active = false;
        this.loseView.active = false;
        this.closeBtn.active = false;
        this.exitBtn.active = false;
        this.gameBtn.active = false;
        this.clickLimit = false;
        clearTimeout(this.timer);
    }

    private closeView() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        this.active = false;
    }

    private onExitClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        Game.Server.send(this.Define.CmdLeave, { "IsClose": 1 });
        DdzDriver.instance.leaveGame();
    }

    private onGameClick() {
        Global.Audio.playGameBundleSound(DdzAudioConst.audioCommonPath + DdzAudioConst.commonAudio.ButtonClick, true);
        if (this.clickLimit)
            return;
        DdzDriver.instance.reMatchPlayer();
        this.clickLimit = true;
        Game.Component.scheduleOnce(() => {
            this.clickLimit = false;
        }, 3);
    }

    public clearByRound(){
        this.active = false;
    }
}

class DdzSettleContent extends DdzBaseView {
    private effect: sp.Skeleton;
    private itemList: DdzSettleItem[] = [];
    private contentNode: cc.Node;
    constructor(node) {
        super();
        this.setNode(node);
    }

    protected initView() {
        this.effect = <sp.Skeleton>this.getComponent('effect', sp.Skeleton);
        this.contentNode = this.getChild('content');
        for (let i = 1; i <= 3; i++) {
            let node = this.getChild('content/item' + i);
            let item = new DdzSettleItem(node);
            this.itemList.push(item);
        }
    }

    protected onOpen() {
        this.effect.setAnimation(0, 'idle', false);
        this.effect.addAnimation(0, 'idle1', true, this.Define.PlaySettleEffectTime);
        this.contentNode.opacity = 1;
        Game.Component.scheduleOnce(() => {
            let action = cc.fadeTo(0.5, 255);
            this.contentNode.runAction(action);
        }, this.Define.PlaySettleEffectTime);
    }

    public showData(arr = [], isSelfWin: boolean, landChair: number) {
        let dzLocalSeat = this.Context.get(this.Define.FieldDzLocSeat);
        if (isSelfWin && dzLocalSeat == 0) {
            arr.sort(user => {
                let flag = (user.chair - landChair) == 0 ? -1 : 1;
                return flag;
            })
        }
        arr.forEach((user, index) => {
            if (this.itemList[index]) {
                this.itemList[index].setStyle(dzLocalSeat, isSelfWin, user);
            }
        })
    }

    protected onClose() {
        this.contentNode.stopAllActions();
    }
}

class DdzSettleItem extends DdzBaseView {
    private checkBg: cc.Node;
    private dzIcon: cc.Node;
    private nameLbl: cc.Label;
    private multLbl: cc.Label;
    private pointLbl: cc.Label;
    constructor(node) {
        super();
        this.setNode(node);
    }

    protected initView() {
        this.checkBg = this.getChild('checkBg');
        this.dzIcon = this.getChild('dzIcon');
        this.nameLbl = <cc.Label>this.getComponent('nameLbl', cc.Label);
        this.multLbl = <cc.Label>this.getComponent('multLbl', cc.Label);
        this.pointLbl = <cc.Label>this.getComponent('pointLbl', cc.Label);
    }

    public setStyle(dzSeat: number, isSelfWin: boolean, data) {
        let localSeat = DdzDriver.instance.SitHelper.serverSToLocalN(data.chair);
        this.dzIcon.active = dzSeat == localSeat;
        this.checkBg.active = localSeat == 0;
        this.multLbl.string = '' + data.total_mult;
        this.pointLbl.string = '' + Global.Toolkit.formatPointStr(data.total_points, false, false);
        let info = this.Context.playerList[localSeat];
        this.nameLbl.string = Global.Toolkit.substrEndWithElli(info.nickname, 10, false);

        let color = new cc.Color(255, 255, 255);
        if (isSelfWin) {
            if (localSeat == 0) {
                color = new cc.Color(255, 197, 148);
            }
            else {
                color = new cc.Color(251, 250, 241);
            }
        }
        else {
            if (localSeat == 0) {
                color = new cc.Color(151, 181, 252);
            }
            else {
                color = new cc.Color(203, 227, 255);
            }
        }
        this.multLbl.node.color = color;
        this.nameLbl.node.color = color;
        this.pointLbl.node.color = color;
    }
}